<template>
  <div id="app">
    <el-input
      v-model="filters[0].value"
      placeholder="输入题目拼音首字母检索"
      clearable/>
    <data-tables :data="data"
      :filters="filters">
      <el-table-column v-for="title in titles"
        :prop="title.prop"
        :label="title.label"
        :key="title.prop"
        :width="title.width">
      </el-table-column>
    </data-tables>
  </div>
</template>

<script>
import PinyinMatch from "pinyin-match";
import DB from './assets/kc.json'
export default {
  components: [
    DB
  ],
  data() {
    return {
      data: DB,
      titles: [
        {
          prop: '答案',
          label: '答案',
          width: '60px'
        },
        {
          prop: '题目内容',
          label: '题目'
        },
        {
          prop: '可选项',
          label: '选项'
        }
      ],
      filters: [
        {
          value: '',
          filterFn: (row, filter) => {
            return Object.keys(row).some(prop => {
              if (prop === '题目内容') {
                return PinyinMatch.match(row[prop], filter.value)
              } else {
                return false
              }
            })
          }
        }
      ]
    }
  }
};
</script>


