import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueDataTables from 'vue-data-tables'
import App from './App.vue'

Vue.use(ElementUI)
Vue.use(VueDataTables)

new Vue({
  el: '#app',
  render: h => h(App)
})



